.side-bar {
    height: 100%;
    background: -webkit-linear-gradient(45deg, rgba(0, 210, 252, 0.4) 20%, transparent 40%,rgba(0, 210, 252, 0.4) 60%,transparent 80%, rgba(0, 210, 252, 0.4) 100%);
    background: linear-gradient(45deg, rgba(0, 210, 252, 0.4) 20%, transparent 40%,rgba(0, 210, 252, 0.4) 60%,transparent 80%, rgba(0, 210, 252, 0.4) 100%);
    box-shadow: 2px 0 5px rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    right: 0;
    width: 60%;
    max-width: 250px;
    z-index: 200;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    -webkit-transition: -webkit-transform 0.5s ease-out;
    transition: -webkit-transform 0.5s ease-out;
    transition: transform 0.5s ease-out;
    transition: transform 0.5s ease-out, -webkit-transform 0.5s ease-out;
}

.side-bar.open {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.side-bar ul {
    height: 70%;
    list-style: none;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}

.side-bar li {
    margin: 0.5rem 1rem;
}

.side-bar a {
    text-decoration: none;
    color: white;
    font-size: 1.2rem;
}

.side-bar a:hover,
.side-bar a:active {
    color: #20E3B2;
}

@media (min-width: 769px) {
    .side-bar {
        display: none;
    }
}
